:root {
  --size: clamp(10rem, 1rem + 30vmin, 10rem);
  --gap: calc(var(--size) / 14);
  --scroll-end: calc(-100% - var(--gap));
}

.marquee {
  display: flex;
  overflow: hidden;
  user-select: none;
  gap: var(--gap);
  mask-image: linear-gradient(
    var(--mask-direction, to right),
    hsl(0 0% 0% / 0),
    hsl(0 0% 0% / 1) 20%,
    hsl(0 0% 0% / 1) 80%,
    hsl(0 0% 0% / 0)
  );
}

.marquee__group {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: var(--gap);
  animation: scroll-x 60s linear infinite;
}

.marquee--reverse .marquee__group {
  animation-direction: reverse;
  animation-delay: -3s;
}

@keyframes scroll-x {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(var(--scroll-end));
  }
}

@keyframes scroll-y {
  from {
    transform: translateY(var(--scroll-start));
  }
  to {
    transform: translateY(var(--scroll-end));
  }
}
/* Element styles */
.marquee .card {
  display: grid;
  place-items: center;
  width: var(--size);
  fill: var(--color-text);
  aspect-ratio: 16/9;
  background-color: rgba(217, 217, 217, 0.05);
  padding: calc(var(--size) / 10);
  border-radius: 1rem;
  min-width: 300px;
}
/* Parent wrapper */
.wrapper {
  display: flex;
  flex-direction: column;
  gap: var(--gap);
  margin: auto;
  max-width: 80vw;
}
@keyframes fade {
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@media screen and (min-width: 280px) {
  .marquee .card{
    min-width: 160px;
  }
}
@media screen and (min-width: 360px) {
  .marquee .card{
    min-width: 190px;
  }
}
@media screen and (min-width: 568px) {
