@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'Rubik';
  src: local('Rubik') url(../public/Rubik-Regular.ttf) format('ttf');
}



@layer base {
  :root {
    --color-text-base:#ffffff;
    --color-text-muted:#f5f5f5;
    --color-text-pink:#FD226A;
    --color-text-inverted:#dc2624;
    --color-text-title:#4E4E4E;
    --color-fill:#070415;
    --color-purple:62, 21, 69;
    --color-black: 0, 0, 0;
    --color-fill-4:217, 217 , 217;

    --color-fill-2:32, 31, 42;
    --color-fill-3:65, 79, 117;
    --color-fill-100:16, 14 , 26;
    --color-fill-200:253, 34, 106; 
    --color-button-accent-hover:#FD226A;
    --color-button-muted:124, 82, 156;
    --second-color:#7C529C; /*  use for */
    --third-color:#43435F; /*  use for button or text or background*/
    --fourth-color:#13111F;/*  */
    --fill-color-indicator:#43f3;/*  */
    --color-border:65, 79, 117;
    --color-border-2:64, 94,255;
   
  }
  .theme-light {
    --color-text-base:#000000;
    --color-text-muted:#f5f5f5;
    --color-text-pink:#FD226A;
    --color-button-accent:#dc2624;
    --color-fill:#ffffff;  
    --color-button-accent:#FD226A; 
    --color-button-accent-hover:#FD226A;
    --color-button-muted:253, 34, 106;
  }
  .theme-purple {
    --color-text-base:#ff84df;
    --color-text-muted:#f5f5f5;
    --color-text-inverted:#dc2624;
    --color-fill:#070415;  
    --color-button-accent:#FD226A; 
    --color-button-accent-hover:#FD226A;
    --color-button-muted:253, 34, 106;
  }
}

*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  zoom: 1;
}
body {
  margin: 0;
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI',  'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-zoom: 1;
}

.fade-anim img {
  animation: fadeAnim 5s;
}
@keyframes fadeAnim {
  from {
    right: 0%;
  }
  to {
    right:100%;
  }
}